<template>
	<div>
		<flow-form
			ref="flowformUsers"
			v-on:complete="(completedBool, questionList) => completed = completedBool"
			v-bind:questions="questions"
			v-bind:standalone="true"
			v-bind:progressbar="false"
			:language="language"
			@submit='onSendData'
		>
			<!-- Custom content for the Complete/Submit screen slots in the FlowForm component -->
			<!-- We've overriden the default "complete" slot content -->
			<template v-slot:complete>
				<div class="f-section-wrap">
					<p>
						<span class="fh2">Yee haw!</span>
						<span class="f-section-text">
							We've tailored the rest to match your needs!
							<br>
							<br>
							Wrangle your latest paystub and bank statement to answer the following questions.
							<br>
							This should take about 5 minutes to complete.
							<span v-if='answers.combinesFinances'>
								Your answers first - we'll ask about your partner a little later.
							</span>
						</span>
					</p>
					<p class="f-description">Note: No financial data will be saved and/or sent.</p>
				</div>  
			</template>

			<!-- We've overriden the default "completeButton" slot content -->
			<!-- this would show up at the very end of this section, not after each question -->
			<template v-slot:completeButton>
				<div class="f-submit">
					<button 
						class="o-btn-action"
						ref="button"
						type="submit"
						href="#"
						v-on:click.prevent="onSendData()"
						aria-label="Press to continue"
					>
						<span>Continue</span>
					</button>
					<a class="f-enter-desc"
						href="#"
						v-on:click.prevent="onSendData()"
						v-html="''">
					</a>
				</div>
			</template>
		</flow-form>
	</div>
</template>

<script>
	import FlowForm, { QuestionModel, QuestionType } from '@ditdot-dev/vue-flow-form'
	import choices from './choices'
	import language from './language'

	export default {
		name: 'UserQuestions',
		components: {
			FlowForm
		},
		data() {
			return {
				language: language,
				submitted: false,
				completed: false,
				// Create question list with QuestionModel instances
				questions: [
					new QuestionModel({
						id: 'break_1',
						title: "Let's answer four questions to customize your Buck Now! experience.",
						content: '',
						description: '',
						type: QuestionType.SectionBreak
					}),
					new QuestionModel({
						id: 'combinesFinances',
						tagline: '',
						title: 'Do you combine finances with a spouse or partner?',
						helpTextShow: false,
						type: QuestionType.MultipleChoice,
						nextStepOnAnswer: true,
						required: true,
						options: choices.yesNoOptions()
					}),
					new QuestionModel({
						id: 'isOld',
						replacePronouns: true,
						tagline: '',
						title: 'Are you age 50 or older?',
						helpTextShow: false,
						type: QuestionType.MultipleChoice,
						nextStepOnAnswer: true,
						required: true,
						options: choices.ageOptions() // defaults to combinesFinances = false
					}),
					new QuestionModel({
						id: 'contributesToHSA',
						replacePronouns: true,
						tagline: '',
						title: 'Are you eligible to contribute to a Health Savings Account ("HSA")?',
						helpTextShow: false,
						type: QuestionType.MultipleChoice,
						nextStepOnAnswer: true,
						required: true,
						options: choices.yesNoOptions()
					}),
					new QuestionModel({
						id: 'isSelfEmployed',
						replacePronouns: true,
						tagline: '',
						title: 'Do you have self-employment income?',
						helpTextShow: false,
						type: QuestionType.MultipleChoice,
						nextStepOnAnswer: true,
						required: true,
						options: choices.yesNoOptions()
					})
				]
			}
		},
		computed: {
			answers() {
				return this.questions.reduce((obj, item) => (obj[item.id] = item.answer, obj), {})
			}
		},
		watch: {
			'answers.combinesFinances'(newVal, oldVal) {
				// if the user combines finances, the question wording needs to be changed from "you" to "either of you"
				let newVerbiage = newVal ? 'either of you' : 'you'
				let oldVerbiage = oldVal ? 'either of you' : 'you'
				// fucking iterate through the questions to update these options like it's the goddamned stone age.
				this.questions.forEach(question => {
					if (question.replacePronouns) {
						question.title = question.title.replace(oldVerbiage, newVerbiage)
					}
					if (question.id === 'isOld') { // swap out the age options to be more precise
						question.options = choices.ageOptions(newVal)
					}
				})
			}
		},
		methods: {
			onSendData() {
				// Set `submitted` to true so the form knows not to allow back/forward
				// navigation anymore.
				this.$refs.flowformUsers.submitted = true
				this.submitted = true

				this.$emit('userInfoComplete', this.answers)
			}
		}
	}
</script>



