import { ChoiceOption } from '@ditdot-dev/vue-flow-form'

export default {
	// these choices must be functions that return fresh choices each time, otherwise they stick across questions
	yesNoOptions: () => [
		new ChoiceOption({
			label: 'yes',
			value: true
		}),
		new ChoiceOption({
			label: 'no',
			value: false
		})
	],
	ageOptions: (combinesFinances = false) => { // Are [you|either of you] over 50?
		if (combinesFinances) {
			return [
				new ChoiceOption({
					label: 'neither of us',
					value: 'neither'
				}),
				new ChoiceOption({
					label: 'just me',
					value: 'primary'
				}),
				new ChoiceOption({
					label: 'just my partner',
					value: 'partner'
				}),
				new ChoiceOption({
					label: 'both of us',
					value: 'both'
				})
			]
		} else {
			return [
				new ChoiceOption({
					label: 'yes',
					value: 'primary'
				}),
				new ChoiceOption({
					label: 'no',
					value: 'neither'
				})
			]
		}
	},
	paycheckFreqOptions: () => [
		new ChoiceOption({
			label: 'every two weeks',
			value: 26
		}),
		new ChoiceOption({
			label: 'twice monthly',
			value: 24
		}),
		new ChoiceOption({
			label: 'monthly',
			value: 12
		})
	],
	_401kOptions: (_401kMaxPossibleString) => [
		new ChoiceOption({
			label: 'none available',
			value: null
		}),
		new ChoiceOption({
			label: '0%',
			value: 0
		}),
		new ChoiceOption({
			label: '3%',
			value: 0.03
		}),
		new ChoiceOption({
			label: '5%',
			value: 0.05
		}),
		new ChoiceOption({
			label: '6%',
			value: 0.06
		}),
		new ChoiceOption({
			label: '10%',
			value: 0.1
		}),
		new ChoiceOption({
			label: '20%',
			value: 0.2
		}),
		new ChoiceOption({
			label: `max: ${_401kMaxPossibleString} annually`,
			value: 1
		})
	],
	IRAOptions: (IRAMaxPossibleInteger, currencyFormatterFunction) => [
		new ChoiceOption({
			label: 'none',
			value: 0
		}),
		new ChoiceOption({
			label: `1/2 max: ${currencyFormatterFunction(IRAMaxPossibleInteger / 2)} annually`,
			value: IRAMaxPossibleInteger / 2
		}),
		new ChoiceOption({
			label: `max: ${currencyFormatterFunction(IRAMaxPossibleInteger)} annually`,
			value: IRAMaxPossibleInteger
		})
	],
	HSAOptions: () => [
		new ChoiceOption({
			label: 'none',
			value: {amount: 0, type: null}
		}),
		new ChoiceOption({
			label: 'individual: $3,650 annually (deducted from pay)',
			value: {amount: 3650, type: 'deducted'}
		}),
		new ChoiceOption({
			label: 'individual: $3,650 annually (contributed out of pocket)',
			value: {amount: 3650, type: 'contributed'}
		}),
		new ChoiceOption({
			label: 'family: $7,300 annually (deducted from pay)',
			value: {amount: 7300, type: 'deducted'}
		}),
		new ChoiceOption({
			label: 'family: $7,300 annually (contributed out of pocket)',
			value: {amount: 7300, type: 'contributed'}
		})
	],
	SEP_IRAOptions: (SEPMaxPossibleString) => [
		new ChoiceOption({
			label: 'none',
			value: 0
		}),
		new ChoiceOption({
			label: '10% of net self-employment income',
			value: 0.5 // half of max, this isn't 10% of the total available but instead 50% of the max
		}),
		new ChoiceOption({
			label: `max: lower of ${SEPMaxPossibleString} or 20%`,
			value: 1 // this is percentage of max, so 100% of the max available.
		})
	]
}


