<template>
  <div id="app" class="container">
    <header class="vff-header">
			<div class="f-container">
        <a href='/'><img src='static/logo.png' class="f-logo"/></a>
			</div>
		</header>
    <UserQuestions 
      v-if='!user'
      @userInfoComplete='info => user = info'
    />
    <Toolkit
      v-if='user'
      :user='user'
      @restart='() => user = null'
    />
  </div>
</template>

<script>
import UserQuestions from './components/UserQuestions'
import Toolkit from './components/Toolkit'
const horseButt = require('@/../static/horse-butt.png')

export default {
  name: 'App',
  components: {
    UserQuestions,
    Toolkit
  },
  data () {
    return {
      user: null
    }
  },
  mounted() {
    // i am tired of wrestling with webpack's css loading getting this image wrong so i am doing it in js.
    document.getElementsByTagName('body')[0].style.backgroundImage = `url('${horseButt}')`
  }
}
</script>

<style>
@import '@ditdot-dev/vue-flow-form/dist/vue-flow-form.css';
@import 'assets/btb-theme.css';

#app {
  font-family: 'mr-eaves-sans', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



