<template>
	<div>
		<flow-form
			ref="flowformSteps"
			v-on:complete="onComplete"
			v-bind:questions="questions"
			v-bind:standalone="true"
			:language="language"
			@submit='onSendData'
		>
			<!-- Custom content for the Complete/Submit screen slots in the FlowForm component -->
			<!-- We've overriden the default "complete" slot content -->
			<template v-slot:complete>
				<div class="f-section-wrap" id="buck-scorecard" v-if='!loading'>
					<span class="fh2" id="buck-scorecard-title">Congratulations...you've Bucked the Budget!</span>
					<div class="f-section-text">
						<h3 id='buck-score-header'>Your Buck score = <span id="big-score">{{finalData.computed.buckScoreDisplayValue}}</span> / 10</h3>
						<a id='buck-score-info' href="https://buckthebudget.com/whats-your-buck-score/" target="_blank">About the Buck score</a>
						<br>
					</div>
					<div class="f-section-text" style="margin-bottom: 0;">
						<b><u>Monthly</u></b>
					</div>
					<div id='buck-results-container'>
						<div>
							<table class="results-table">
								<tr class="big-row">
									<td>Step 1: Paid yourself</td>
									<td>{{formatCurrency(finalData.computed.payYourself)}}</td>
									<td>{{finalData.computed.buckScorePercentages.step1}}%</td>
								</tr>
								<tr class="little-row" v-if='version == "premium"'>
									<td>401(k)</td>
									<td>{{formatCurrency(((finalData.computed.contrib401k / 12) + (finalData.computed.contrib401kPartner / 12)))}}</td>
									<td></td>
								</tr>
								<tr class="little-row" v-if='version == "premium"'>
									<td>IRAs</td>
									<td>{{formatCurrency((((finalData.answers.contribIRA || 0) / 12) + ((finalData.answers.contribIRAPartner || 0) / 12)) + (((finalData.answers.contribSEPIRAPercentage || 0) * finalData.computed.SEPMaxAllowed) / 12) + (((finalData.answers.contribSEPIRAPercentagePartner || 0) * finalData.computed.SEPMaxAllowedPartner) / 12))}}</td>
									<td></td>
								</tr>
								<tr class="little-row" v-if='version == "premium"'>
									<td>HSA</td>
									<td>{{formatCurrency(((finalData.computed.contribHSADeducted / 12) + (finalData.computed.contribHSAContributed / 12)))}}</td>
									<td></td>
								</tr>
								<tr class="big-row">
									<td>Step 2: Wrangled living costs</td>
									<td>{{formatCurrency(finalData.computed.totalLivingCosts)}}</td>
									<td>{{finalData.computed.buckScorePercentages.step2}}%</td>
								</tr>
								<tr class="little-row" v-if='version == "premium"'>
									<td>Emergency fund contribution</td>
									<td>{{formatCurrency(finalData.computed.monthlyAddToEmergencyFund)}}</td>
									<td></td>
								</tr>
								<tr class="little-row" v-if='version == "premium"'>
									<td>Debts and other commitments</td>
									<td>{{formatCurrency(0)}}</td>
									<td></td>
								</tr>
								<tr class="little-row" v-if='version == "premium"'>
									<td>Other living costs</td>
									<td>{{formatCurrency(finalData.computed.sumOfMonthlyExpenses)}}</td>
									<td></td>
								</tr>
								<tr class="big-row">
									<td>Step 3: 
										<span v-show='(finalData.computed.spendTheRest > 0) || (finalData.computed.spendTheRest === 0)'>
											Now spend the rest!
										</span>
										<span v-show='finalData.computed.spendTheRest < 0' id='buck-fail'>
											...but have nothing left to spend (oops!). <a v-on:click.prevent="onSendData()">Buck again?</a>
										</span>
									</td>
									<td>{{formatCurrency(finalData.computed.spendTheRest)}}</td>
									<td>{{finalData.computed.buckScorePercentages.step3}}%</td>
								</tr>
							</table>
						</div>
						<div v-if='version == "premium"'>
							<img id="barbed-wire-img" src='static/wire.png'/>
							<div id='improve-score-title'>Ways to increase your score:</div>
							<ul id='improve-score-items'>
								<li v-for="(item, index) in finalData.computed.buckScoreMissingItems" :key="`item-${index}`">
									{{item}}
								</li>
							</ul>
						</div>
					</div>
				</div>
				<div class="f-section-wrap" id="buck-scorecard" v-else>
					<div class="spinner">
						<div class="bounce1"></div>
						<div class="bounce2"></div>
						<div class="bounce3"></div>
					</div>
				</div>

			</template>

			<!-- We've overriden the default "completeButton" slot content -->
			<template v-slot:completeButton>
				<div class="f-submit" id="buck-again-container" v-show='!loading'>
					<div v-if='version == "premium"'>
						<button 
							id="buck-again-button"
							class="o-btn-action"
							ref="button"
							type="submit"
							href="#"
							v-on:click.prevent="onSendData()"
							aria-label="Press to start over"
						>
							<span>Buck Again</span>
						</button>
						<br>
						<br>
						<br>
						<a href='/' id='back-home-link'>Back to Home</a>
					</div>
					<div v-else>
						<p>
							Want more details and specific recommendations?
							<br>
							Buy access to our premium <i>Buck Now!</i> web app:
						</p>
						<button 
							class="o-btn-action"
							id="upgrade-button"
							ref="button"
							type="submit"
							@click='linkOut'
							aria-label="Upgrade to Premium"
						>
							<span>Join for Premium</span>
						</button>
					</div>
					<br>
					<div class='debug' v-if='debug'>
						<h3>debug</h3>
						<b><u>version:</u></b>
						<select v-model="version">
							<option>free</option>
							<option>premium</option>
						</select>
						<br>
						<b><u>user data</u></b>
						<div v-for='(value, key, index) in finalData.user' :key='`${index}${key}`'>
							<b>{{key}}:</b> {{value}}
							<br>
						</div>
						<br>
						<b><u>user inputs</u></b>
						<div v-for='(value, key, index) in finalData.answers' :key='`${index}${key}`'>
							<b>{{key}}:</b> {{value}}
							<br>
						</div>
						<br>
						<b><u>computed values</u></b>
						<div v-for='(value, key, index) in finalData.computed' :key='`${index}${key}`'>
							<b>{{key}}:</b> {{value}}
							<br>
						</div>
					</div>
				</div>
			</template>
		</flow-form>
	</div>
</template>

<script>
	import FlowForm, { QuestionModel, QuestionType } from '@ditdot-dev/vue-flow-form'
	import choices from './choices'
	import language from './language'
	import percentRound from 'percent-round'

	export default {
		name: 'Toolkit',
		components: {
			FlowForm
		},
		props: ['user'],
		data() {
			return {
				submitted: false,
				completed: false,
				language: language,
				finalData: {
					user: {},
					answers: {},
					computed: {buckScorePercentages: {}}
				},
				version: 'free',
				allBuckScoreItems: {
					payYourself: 'Strive to pay yourself at least 20%.',
					contrib401k: 'Contribute to your 401(k).',
					contrib401kPartner: 'Contribute to your 401(k) - partner.',
					contribIRA: 'Contribute to your IRA.',
					contribIRAPartner: 'Contribute to your IRA - partner.',
					contribHSA: 'Contribute to a Health Savings Account.',
					lowLivingCosts: 'Strive to keep living costs below 50%.',
					emergencyFund: "Keep an emergency fund of at least 3 months' living costs.",
					reasonableSpending: 'Go back and further wrangle living costs to afford more spending.'
				},
				loading: true,
				debug: false
			}
		},
		computed: {
			questions() {
				// build the questions array since it is a fucking array. only fires once since the user prop doesn't change.

				// step 1 is the same for you/your partner, so we can iterate to add those questions by building
				// an array of one or two {variableSuffix, wording} objects.
				let personObjs = [{variableSuffix: '', wording: 'do you'}]
				if (this.user && this.user.combinesFinances) {
					// there are two of you; you'll go first and then your partner
					personObjs.push({variableSuffix: 'Partner', wording: 'does your partner'})
				}

				// we'll add all the questions from step 1 here as we iterate
				let step1Questions = []

				personObjs.forEach(person => {
					let _personalContribLimits = person.variableSuffix === '' ? this.contributionLimits.primary : this.contributionLimits.partner

					// these happen for everyone regardless of their UserQuestions
					step1Questions = step1Questions.concat([
						new QuestionModel({
							id: `paycheckFreq${person.variableSuffix}`,
							title: `How often ${person.wording} receive a paycheck?`,
							helpTextShow: false,
							type: QuestionType.MultipleChoice,
							nextStepOnAnswer: true,
							required: true,
							options: choices.paycheckFreqOptions()
						}),
						new QuestionModel({
							id: `contrib401kPercentage${person.variableSuffix}`,
							title: `How much ${person.wording} contribute to a 401(k) (or other) employer plan?`,
							subtitle: 'Exclude employer match or profit sharing.',
							helpTextShow: false,
							type: QuestionType.MultipleChoice,
							nextStepOnAnswer: true,
							required: true,
							options: choices._401kOptions(this.formatCurrency(_personalContribLimits._401k))
						}),
						new QuestionModel({
							id: `grossPaycheckAmount${person.variableSuffix}`,
							title: `How much ${person.wording} get paid each paycheck?`,
							subtitle: 'Gross pay before taxes and deductions.',
							type: QuestionType.Number,
							placeholder: 'Type your $ here...',
							required: true,
							min: 0,
							max: 500000
						}),
						new QuestionModel({
							id: `netPaycheckAmount${person.variableSuffix}`,
							title: `How much ${person.wording} net each paycheck?`,
							subtitle: 'Take-home pay after taxes and deductions.',
							type: QuestionType.Number,
							placeholder: 'Type your $ here...',
							required: true,
							min: 0,
							max: 500000
						}),
						new QuestionModel({
							id: `otherIncome${person.variableSuffix}`,
							title: `How much ${person.wording} earn monthly from rentals or investments?`,
							subtitle: 'Exclude earnings on retirement accounts.  For rental income, include rental receipts less mortgage and any other payments.',
							type: QuestionType.Number,
							placeholder: 'Type your $ here...',
							required: true,
							min: 0,
							max: 500000
						}),
						new QuestionModel({
							id: `contribIRA${person.variableSuffix}`,
							title: `How much ${person.wording} expect to contribute to an Individual Retirement Account ("IRA")?`,
							subtitle: (this.user && this.user.isSelfEmployed) ? 'Separate from an SEP-IRA.' : '',
							helpTextShow: false,
							type: QuestionType.MultipleChoice,
							nextStepOnAnswer: true,
							required: true,
							options: choices.IRAOptions(_personalContribLimits.IRA, this.formatCurrency)
						})
					])

					// add hsa to step one if you've got it. this ONLY gets added for you, not your partner
					// by adding the variableSuffix test we only add this q for you. remove that and it'll work for both you/partner.
					if (this.user && this.user.contributesToHSA && person.variableSuffix === '') {
						step1Questions.push(
							new QuestionModel({
								id: `contribHSA${person.variableSuffix}`,
								title: `How much ${person.wording} expect to contribute to a Health Savings Account?`,
								subtitle: 'Exclude employer contributions, if any.',
								helpTextShow: false,
								type: QuestionType.MultipleChoice,
								nextStepOnAnswer: true,
								required: true,
								options: choices.HSAOptions()
							})
						)
					}

					// add self employed to step one for BOTH you/partner if you have got it
					if (this.user && this.user.isSelfEmployed) {
						step1Questions = step1Questions.concat([
							new QuestionModel({
								id: `selfEmployEarnings${person.variableSuffix}`,
								title: `What ${person.wording} average in net self-employment earnings each month?`,
								subtitle: 'After expenses.',
								type: QuestionType.Number,
								placeholder: 'Type your $ here...',
								required: true,
								min: 0,
								max: 500000
							}),
							new QuestionModel({
								id: `taxAsideAmount${person.variableSuffix}`,
								title: `How much ${person.wording} set aside monthly for taxes?`,
								subtitle: '',
								type: QuestionType.Number,
								placeholder: 'Type your $ here...',
								required: true,
								min: 0,
								max: 500000
							}),
							new QuestionModel({
								id: `contribSEPIRAPercentage${person.variableSuffix}`,
								title: `How much ${person.wording} expect to contribute to a Simplified Employee Pension ("SEP-IRA")?`,
								helpTextShow: false,
								type: QuestionType.MultipleChoice,
								nextStepOnAnswer: true,
								required: true,
								options: choices.SEP_IRAOptions(this.formatCurrency(_personalContribLimits.SEP))
							})
						])
					}

					// if we're going to tack on partner questions next, let's put a break to make it obvious.
					if (this.user && this.user.combinesFinances && person.variableSuffix === '' && personObjs.length > 1) {
						step1Questions.push(
							new QuestionModel({
								id: 'breakBetweenPartners',
								title: "You're doing great!",
								content: "We're done with your information, now let's enter some info for your partner.",
								description: '',
								type: QuestionType.SectionBreak
							})
						)
					}
				}) // end of forEach that populates step1Questions

				// add a secton break to the end of step 1
				step1Questions.push(
					new QuestionModel({
						id: 'breakBetweenSteps',
						title: "That's it for Step One!",
						content: "Now that we've established your pay, let's wrangle your living costs.",
						description: '',
						type: QuestionType.SectionBreak
					})
				)

				let step2Questions = [
					new QuestionModel({
						id: 'emergencyFund',
						title: 'How much is in your emergency fund?',
						subtitle: '',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'housingPayment',
						title: 'How much do you pay each month for your rent or mortgage?',
						subtitle: '',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'utilityPayment',
						title: 'How much do you pay each month for utilities?',
						subtitle: 'Include electric/gas, internet, alarm, trash, HOA, cell phone.',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'householdItemsPayment',
						title: 'How much do you pay each month for groceries and household items?',
						subtitle: 'Typically $200-400 per person.',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'medicalPayment',
						title: 'How much do you pay each month in medical expenses?',
						subtitle: 'Insurance premiums (if not already deducted from your paycheck), prescriptions, co-pays.',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'transportationPayment',
						title: 'How much do you pay each month for transportation?',
						subtitle: 'Car payment, insurance, gas, maintenance, parking, bus fare.',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'otherDebtsPayment',
						title: 'How much do you pay each month for other debt or commitments?',
						subtitle: 'Student loans, credit cards, Netflix, gym membership.',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					}),
					new QuestionModel({
						id: 'otherUncategorizedPayment',
						title: 'How much do you pay each month for other things?',
						subtitle: 'Childcare, etc. Committed costs only; exclude discretionary spending.',
						type: QuestionType.Number,
						placeholder: 'Type your $ here...',
						required: true,
						min: 0,
						max: 500000
					})
				]
				return step1Questions.concat(step2Questions)
			},
			answers() {
				return this.questions.reduce((obj, item) => (obj[item.id] = item.answer, obj), {})
			},
			contributionLimits() { // retirement contrib limits for both you and partner
				let old = { // over 50, you can add more
					_401k: 27000,
					IRA: 7000,
					SEP: 67500
				}
				let young = { // under 50, you can add less
					_401k: 20500,
					IRA: 6000,
					SEP: 61000
				}

				if (this.user && this.user.isOld === 'neither') {
					return {primary: young, partner: young}
				} else if (this.user && this.user.isOld === 'primary') {
					return {primary: old, partner: young}
				} else if (this.user && this.user.isOld === 'partner') {
					return {primary: young, partner: old}
				} else {
					return {primary: old, partner: old}
				}
			},
			buckScoreMaxPossbile() {
				let score = 5 // it can never be less than 5
				// these are the items possible. this is used to tell which ones you didn't get.
				let items = [
					this.allBuckScoreItems.payYourself,
					this.allBuckScoreItems.contribIRA,
					this.allBuckScoreItems.emergencyFund,
					this.allBuckScoreItems.lowLivingCosts,
					this.allBuckScoreItems.reasonableSpending
				]
				
				// Contribute to your 401(k) if available
				if (this.answers.contrib401kPercentage !== 'none available') {
					score++
					items.push(this.allBuckScoreItems.contrib401k)
				}
				// same for partner. skip if this never gets set
				if (this.answers.hasOwnProperty('contrib401kPercentagePartner') && this.answers.contrib401kPercentagePartner !== 'none available') {
					score++
					items.push(this.allBuckScoreItems.contrib401kPartner)
				}
				// Contribute to an IRA - partner. if you have a partner this is an available option to you
				if (this.user && this.user.combinesFinances) {
					score++
					items.push(this.allBuckScoreItems.contribIRAPartner)
				}
				// Contribute to an HSA.
				if (this.user && this.user.contributesToHSA) {
					score++
					items.push(this.allBuckScoreItems.contribHSA)
				}
				return {score, items}
			},
			buckScoreEarned() {
				let score = 0
				let items = []

				// Pay yourself at least 20%. If PayYourself >= 20% (of MonthlyTakeHomePrior401k), =1 , otherwise 0
				if (this.payYourself >= (this.monthlyTakeHomePrior401k * 0.2)) {
					score++
					items.push(this.allBuckScoreItems.payYourself)
				}
				// Contribute to your 401(k). If Contrib401k >0, =1, otherwise 0
				if (this.contrib401k > 0) { // "none available" > 0 == false
					score++
					items.push(this.allBuckScoreItems.contrib401k)
				}
				// Contribute to  401(k) - partner. If Contrib401kPartner >0, =1, otherwise 0
				if (this.contrib401kPartner > 0) { // "none available" > 0 == false
					score++
					items.push(this.allBuckScoreItems.contrib401kPartner)
				}
				// Contribute to an IRA. If (ContribIRA + ContribSEP) > 0, =1, otherwise 0
				if ((this.answers.contribIRA + ((this.answers.contribSEPIRAPercentage || 0) * this.SEPMaxAllowed)) > 0) {
					score++
					items.push(this.allBuckScoreItems.contribIRA)
				}
				// Contribute to an IRA - partner. If (ContribIRAPartner + ContribSEPPartner) > 0, =1, otherwise 0
				if ((this.answers.contribIRAPartner + ((this.answers.contribSEPIRAPercentagePartner || 0) * this.SEPMaxAllowedPartner)) > 0) {
					score++
					items.push(this.allBuckScoreItems.contribIRAPartner)
				}
				// Contribute to an HSA. If (contribHSADeducted + contribHSAContributed) > 0, =1, otherwise 0
				if ((this.contribHSADeducted + this.contribHSAContributed) > 0) {
					score++
					items.push(this.allBuckScoreItems.contribHSA)
				}
				// Emergency fund of at least 3 months living costs. If monthlyAddToEmergencyFund =0, =1, otherwise 0
				if (this.monthlyAddToEmergencyFund === 0) {
					score++
					items.push(this.allBuckScoreItems.emergencyFund)
				}
				// Living costs below 50%. If sumOfMonthlyExpenses < 50% (of MonthlyTakeHomePrior401k), =1, otherwise 0
				if (this.sumOfMonthlyExpenses < (this.monthlyTakeHomePrior401k * 0.5)) {
					score++
					items.push(this.allBuckScoreItems.lowLivingCosts)
				}
				// Spending of at least 5%. If SpendTheRest >= 5% (of MonthlyTakeHomePrior401k), =1, otherwise 0
				if (this.spendTheRest >= (this.monthlyTakeHomePrior401k * 0.05)) {
					score++
					items.push(this.allBuckScoreItems.reasonableSpending)
				}

				return {score, items}
			},
			buckScoreDisplayValue() {
				return Math.abs(((Math.round((this.buckScoreEarned.score / this.buckScoreMaxPossbile.score) * 100) / 10) - 0.1)).toFixed(1)
			},
			buckScorePercentages() {
				let percentages = percentRound([this.payYourself, this.totalLivingCosts, this.spendTheRest])
				return {
					step1: percentages[0] || 0,
					step2: percentages[1] || 0,
					step3: percentages[2] || 0
				}
			},
			buckScoreMissingItems() {
				return this.buckScoreMaxPossbile.items.filter(buckScoreItem => !this.buckScoreEarned.items.includes(buckScoreItem))
			},
			// these are step 1 computations. these 4 ___Partner() props could probably be rolled into the base ones, but it
			// wouldn't make it THAT much shorter and it'd be harder to read. but the non-DRYness of this is fucking killing me
			annualSalary() { // how often you get paid times how much you get paid
				// returns 0 if for some reason the math results in NaN
				return (this.answers.paycheckFreq * this.answers.grossPaycheckAmount) || 0
			},
			annualSalaryPartner() {
				// returns 0 if for some reason the math results in NaN
				return (this.answers.paycheckFreqPartner * this.answers.grossPaycheckAmountPartner) || 0
			},
			contrib401k() { // converts your percentage contribution to your 401k into a number. cannot exceed max limit set in contributionLimits
				// returns 0 if for some reason the math results in NaN
				let value = (this.answers.contrib401kPercentage * this.annualSalary) || 0
				return value > this.contributionLimits.primary._401k ? this.contributionLimits.primary._401k : value
			},
			contrib401kPartner() {
				// returns 0 if for some reason the math results in NaN
				let value = (this.answers.contrib401kPercentagePartner * this.annualSalaryPartner) || 0
				return value > this.contributionLimits.partner._401k ? this.contributionLimits.partner._401k : value
			},
			SEPEligibleEarnings() {
				let earnings = this.answers.selfEmployEarnings || 0
				return (earnings * 12) - (earnings * 12 * 0.9235 * (0.153 / 2))
			},
			SEPEligibleEarningsPartner() {
				let earnings = this.answers.selfEmployEarningsPartner || 0
				return (earnings * 12) - (earnings * 12 * 0.9235 * (0.153 / 2))
			},
			SEPMaxAllowed() {
				// initally only can contribute 20% to SEP-IRA
				let value = (this.SEPEligibleEarnings * 0.2) || 0

				// you can only contribute up to the max value, less your contributions to your 401k
				if (value > this.contributionLimits.primary.SEP - this.contrib401k) {
					value = this.contributionLimits.primary.SEP - this.contrib401k
				}

				return value
			},
			SEPMaxAllowedPartner() {
				// initally only can contribute 20% to SEP-IRA
				let value = (this.SEPEligibleEarningsPartner * 0.2) || 0

				// you can only contribute up to the max value, less your contributions to your 401k
				if (value > this.contributionLimits.partner.SEP - this.contrib401kPartner) {
					value = this.contributionLimits.partner.SEP - this.contrib401kPartner
				}

				return value
			},
			contribHSADeducted() { // for simplicty, split HSA into two vars based on deducted or contributed
				// this is how her formula had it and it's not worth it to rework around.
				if (this.answers && this.answers.contribHSA && this.answers.contribHSA.type === 'deducted') {
					return this.answers.contribHSA.amount
				} else {
					return 0
				}
			},
			contribHSAContributed() { // for simplicty, split HSA into two vars based on deducted or contributed
				// this is how her formula had it and it's not worth it to rework around.
				if (this.answers && this.answers.contribHSA && this.answers.contribHSA.type === 'contributed') {
					return this.answers.contribHSA.amount
				} else {
					return 0
				}
			},
			monthlyTakeHomePrior401k() { // this is the sum of all 3 steps and the denominator used to calculate % for each step
				let value = (((this.answers.netPaycheckAmount || 0) * (this.answers.paycheckFreq || 0)) / 12) + (this.contrib401k / 12)
				value += (((this.answers.netPaycheckAmountPartner || 0) * (this.answers.paycheckFreqPartner || 0)) / 12) + (this.contrib401kPartner / 12)
				value += (this.answers.otherIncome || 0) + (this.answers.otherIncomePartner || 0) 
				value += this.contribHSADeducted / 12
				value += (this.answers.selfEmployEarnings || 0) - (this.answers.taxAsideAmount || 0)
				value += (this.answers.selfEmployEarningsPartner || 0) - (this.answers.taxAsideAmountPartner || 0)
				return value
			},
			payYourself() { // the result from step 1
				let value = (this.contrib401k / 12) + (this.contrib401kPartner / 12)
 				value += ((this.answers.contribIRA || 0) / 12) + ((this.answers.contribIRAPartner || 0) / 12)
				value += (this.contribHSADeducted / 12) + (this.contribHSAContributed / 12)
				value += ((this.answers.contribSEPIRAPercentage || 0) * this.SEPMaxAllowed) / 12
				value += ((this.answers.contribSEPIRAPercentagePartner || 0) * this.SEPMaxAllowedPartner) / 12
				return value
			},
			sumOfMonthlyExpenses() {
				let value = (this.answers.housingPayment || 0) + (this.answers.utilityPayment || 0) + (this.answers.householdItemsPayment || 0)
				value += (this.answers.medicalPayment || 0) + (this.answers.transportationPayment || 0) + (this.answers.otherDebtsPayment || 0)
				value += (this.answers.otherUncategorizedPayment || 0)
				return value
			},
			monthlyAddToEmergencyFund() {
				let _3xMonthlyExpenses = this.sumOfMonthlyExpenses * 3
				if ((this.answers.emergencyFund || 0) >= _3xMonthlyExpenses) {
					return 0
				} else {
					return (_3xMonthlyExpenses - (this.answers.emergencyFund || 0)) / 12
				}
			},
			totalLivingCosts() { // the result from step 2
				return this.sumOfMonthlyExpenses + this.monthlyAddToEmergencyFund
			},
			spendTheRest() { // the result of step 3
				return this.monthlyTakeHomePrior401k - this.totalLivingCosts - this.payYourself
			}
		},
		methods: {
			onSendData() {
				// Set `submitted` to true so the form knows not to allow back/forward
				// navigation anymore.
				this.$refs.flowformSteps.submitted = true
				this.submitted = true

				this.$emit('restart')
			},
			onComplete(completedBool, questionList) {
				this.completed = completedBool

				// freeze computed props in time
				let computed = {}
				Object.keys(this.$options.computed).forEach(key => {
					if (!['answers', 'questions'].includes(key)) {
						computed[key] = this[key]
					}
				})

				// for some reason accessing this data directly on the final screen messes with vue's reactivity.
				// if we copy it and then access it at the end, it still works. who knows...
				this.finalData = {
					user: this.user,
					answers: this.answers,
					computed: computed
				}

				// wait 5 secs for cute loading gif
				setTimeout(() => this.loading = false, 4000)
			},
			formatCurrency(number) { // rounds all decimals and formats like $x,xxx
				if (number !== undefined) {
					number = Math.round(number) || 0
					return number.toLocaleString('en-US', {style: 'currency', currency: 'USD'}).replace('.00', '')
				}
				return '$0'
			},
			linkOut() {
				// prevent onbeforeunload from going off by telling it we're done
				this.$refs.flowformSteps.submitted = true
				this.submitted = true
				window.onbeforeunload = null // vff sets this and doesn't unset it since our last page is technically pre-submission.
				window.location.href = "https://buckthebudget.com/#options"
			}
		},
		created() {
			if (window.location.search.includes('yeehaw')) {
				this.version = 'premium'
			}
			if (window.location.search.includes('debuck')) {
				this.debug = true
			}
		},
	}
</script>
<style scoped>
	.debug {
		color: aquamarine;
		font-size: x-large;
		text-align: left;
	}
	.debug select {
		opacity: 1;
		background: white;
		position: unset;
		width: auto;
	}
	#big-score {
		color: var(--vff-main-accent-color);
		font-weight: bold;
    	font-size: xxx-large;
	}
	#buck-scorecard, #buck-again-container {
		text-align: center;
	}
	#buck-scorecard-title {
		padding-right: 0;
	}
	.results-table {
		width: -moz-available;          /* WebKit-based browsers will ignore this. */
		width: -webkit-fill-available;  /* Mozilla-based browsers will ignore this. */
		width: fill-available;
	}
	.results-table>tr.big-row,
	#improve-score-items {
		font-size: x-large;
		font-weight: bold;
	}
	.results-table>tr.little-row {
		font-size: larger;
	}
	.results-table>tr td:first-child {
		text-align: left;
	}
	.results-table>tr.big-row td:first-child {
		padding-right: 15vw;
		line-height: normal;
	}
	.results-table>tr.little-row td:first-child {
		padding-left: 5vw;
	}
	.results-table>tr td:nth-child(2) {
		text-align: left;
	}
	.results-table>tr.big-row td:nth-child(2) {
		padding-right: 3vw;
		color: var(--vff-main-accent-color);
	}
	#upgrade-button,
	#buck-again-button {
		text-transform: none;
	}
	#barbed-wire-img {
		margin-top: 20px;
		margin-bottom: 20px;
	}
	#improve-score-title {
		padding-right: 0;
		color: var(--vff-main-accent-color);
		font-weight: bold;
		font-size: xx-large;
	}
	#improve-score-items {
		text-align: left;
		padding-left: 25px;
	}
	#improve-score-items>li {
		font-size: x-large;
	}
	@media (min-width: 700px) {
		#buck-results-container {
			padding-left: 12.5%;
			padding-right: 12.5%;
		}
	}

	@media (max-width: 700px) {
		/* add padding to results header rows on mobile */
		.results-table>tr.big-row td {
			padding-top: 20px;
		}
	}

	#buck-again-container p {
		font-size: 1.5em;
	}

	#buck-fail {
		color: var(--vff-main-accent-color);
	}

	#buck-score-info {
		font-size: 0.7em;
		color: var(--vff-main-accent-color);
	}

	#buck-score-header {
		line-height: 0.5;
	}

	#back-home-link {
		color: var(--vff-secondary-text-color);
		font-family: mrs-eaves;
		font-size: 22px;
		font-weight: bold;
	}

	/* loading dots css */
	.spinner {
		margin: 100px auto 0;
		width: 70px;
		text-align: center;
	}

	.spinner > div {
		width: 18px;
		height: 18px;
		background-color: var(--vff-main-accent-color);

		border-radius: 100%;
		display: inline-block;
		-webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
		animation: sk-bouncedelay 1.4s infinite ease-in-out both;
	}

	.spinner .bounce1 {
		-webkit-animation-delay: -0.32s;
		animation-delay: -0.32s;
	}

	.spinner .bounce2 {
		-webkit-animation-delay: -0.16s;
		animation-delay: -0.16s;
	}

	@-webkit-keyframes sk-bouncedelay {
		0%, 80%, 100% { -webkit-transform: scale(0) }
		40% { -webkit-transform: scale(1.0) }
	}

	@keyframes sk-bouncedelay {
		0%, 80%, 100% { 
			-webkit-transform: scale(0);
			transform: scale(0);
		} 40% { 
			-webkit-transform: scale(1.0);
			transform: scale(1.0);
		}
	}
</style>


